@import "../../../styles/vars";
@import "../../../styles/mixins";

.wrapperWithImg {
  @include flexCol(space-between);

  @include lgScreenMedia {
    flex-direction: column;
  }

  &_lg {
    @include setDistance(padding-top, 60);
    @include setDistance(padding-bottom, 60);
  }

  &_sm {
    @include setDistance(padding-top, 70);
    @include setDistance(padding-bottom, 70);
  }

  &_def {
    gap: 178px;

    @include xxlScreenMedia {
      gap: 131px;
    }

    @include xlScreenMedia {
      gap: 115px;
    }

    @include lgScreenMedia {
      gap: 55px;
    }

    @include smScreenMedia {
      gap: 20px;
    }
  }

  &_reverse {
    flex-direction: row-reverse;

    @include lgScreenMedia {
      flex-direction: column;
    }
  }

  &_reverseMob {
    @include lgScreenMedia {
      flex-direction: column-reverse;
    }
  }

  &__imgBlock {
    width: 50%;
    @include flexCol(flex-end);

    @include lgScreenMedia {
      width: 100%;
      justify-content: center;
    }
  }

  &__img {
    width: 100%;
    max-height: 886px;
    object-fit: contain;
  }

  &__textBlock {
    width: 100%;

    @include lgScreenMedia {
      max-width: 705px!important;
      @include flexCol();
      flex-direction: column;
      text-align: center;
    }

    @include smScreenMedia {
      text-align: left;
    }

    &_lg {
      max-width: 672px;
      width: calc(50% - 178px);

      @include xxlScreenMedia {
        width: calc(50% - 131px);
      }

      @include xlScreenMedia {
        width: calc(50% - 115px);
      }

      @include lgScreenMedia {
        max-width: none;
        width: 100%;
      }
    }

    &_md {
      max-width: 530px;

      @include xxlScreenMedia {
        max-width: 434px;
      }

      @include xlScreenMedia {
        max-width: 336px;
      }
    }

    &_sm {
      max-width: 526px;

      @include xxlScreenMedia {
        max-width: 434px;
      }

      @include xlScreenMedia {
        max-width: 336px;
      }
    }
  }

  &__title {
    margin-bottom: 40px;
    color: $color-white;

    @include xxlScreenMedia {
      margin-bottom: 30px;
    }

    @include lgScreenMedia {
      margin-bottom: 26px;
    }

    @include smScreenMedia {
      margin-bottom: 20px;
    }
  }

  &__textWrapper {
    @include flexCol(flex-start, flex-start);
    gap: 28px;
    flex-direction: column;

    @include lgScreenMedia {
      text-align: left;
    }

    @include smScreenMedia {
      gap: 20px;
    }
  }

  &__contentText {
    display: flex;
    flex-direction: column;
    gap: 10px;

    @include lgScreenMedia {
      text-align: center;
    }
  }

  &__btn {
    margin-top: 30px;

    @include xxlScreenMedia {
      margin-top: 26px;
    }

    @include xlScreenMedia {
      margin-top: 23px;
    }

    @include lgScreenMedia {
      margin-top: 18px;
    }
  }
}
